<template>
  <div class="addrList">
  	<h2><span class="arrow_left" onclick="window.history.go(-1)"></span>收货地址</h2>
  	<ul class="addrList_ul">
  		<li  v-for="item in addrList" v-bind:data-id="item.id" @click="to_sublime_order($event)">
  			<p>
  				<span>收货人:<i>{{item.name}}</i></span>
  				<em v-if="item.isDefault">默认</em>
				<b>{{item.phone}}</b>
  			</p>
  			<p>
          <span>地址:</span>{{item.region |capitalize}}{{item.address}}
  			</p>
  		</li>
  	</ul>
  	<div id="increase_new_addr" @click="increase_new_addr">
  		添加新地址
  	</div>
  </div>
</template>

<script>
import { Toast } from 'mint-ui';
export default {
  name: 'Addr-list',
  data () {
    return {
      addrList:[],
      receiver:{}
    }
  },
  created(){
    this.get_addList_ajax();
  },
  methods:{
  	increase_new_addr(){
  		this.$router.push({
        path:'/NewAddr'
      })
  	},
    get_addList_ajax(){
      this.$http({
        method:'get',
        url:this.$api+'/h5/users/address/list'
      }).then(res=>{
        
        if(res.data.succ){
          this.addrList = res.data.value
        }
        console.log(res.data.value);
      }).catch(error=>{
          let instance = Toast('网络好像有问题,请稍候再试试');
        setTimeout(() => {
          instance.close();
        }, 2000);
        })
    },
    to_sublime_order(event){
      console.log(event.currentTarget.dataset.id);
      this.addrList.forEach((item,index)=>{
        if(item.id == event.currentTarget.dataset.id){
          this.$set(this,'receiver',{
            'name':item.name,
            'phone':item.phone,
            'region':item.region,
            'address':item.address
          });
        }
      })
      this.$store.commit('newReceiver_data',this.receiver);
      window.sessionStorage.receiver_data = JSON.stringify(this.receiver);
      this.$router.push({
        path:'./submit_order'
      })
    }
  },
  filters:{
    capitalize(value){
      return value.split(';').join('')
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang='scss' scoped>
@import '../common/css/var.scss';
h2{
  width: 100%;
  height: 44px;
  line-height: 44px;
  font-family:PingFangSC-Regular;
  font-size:18px;
  color:#212121;
  text-align: center;
  position: relative;
  box-sizing:border-box;
  border-bottom: 1px solid #f4f4f4;/*no*/
  .arrow_left{
    position: absolute;
    width: 24px;
    height: 24px;
    top: 10px;
    left: 20px;
    background: url('../assets/order_navbar_arrow_back_icon@3x.png');
    background-size:100%; 
  }
}
.addrList_ul{
  max-width: 414px;
    margin:0 auto;
	width: 100%;
	box-sizing:border-box;
	padding:0 20px 48px;
	font-size: 14px;
	color: $fontColor; 
	li{
		height: 109px;
		border-bottom: 1px solid #f4f4f4;/*no*/
		p:nth-child(1){
			height: 54px;
			line-height: 54px;
			display: flex;
			justify-content:space-between;
			align-items:center;
			span{
				padding-right: 20px;
			}
			em{
				width: 40px;
				text-align: center;
				font-size: 12px;
				border:1px solid $pink;/*no*/
				height: 20px;
				line-height: 20px;
				color: $pink;
				border-radius: 3px;
				transform: scale(0.8);
			}
			b{
			}
		}
		p:nth-child(2){
			line-height: 20px;
			display: -webkit-box;
			-webkit-box-orient: vertical;
			-webkit-line-clamp: 2;
			overflow: hidden;
		}
	}
}
#increase_new_addr{
	position: fixed;
	bottom: 0;
	left:0;
	color: white;
	width: 100%;
	text-align: center;
	font-size: 18px;
	height: 48px;
	line-height: 48px;
	background: $fontColor;
  transform: translate3d(0,0,0);
}
</style>



